* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}
.cartcontainer {
  padding: 40px 50px;
  width: 1005;
  background: #f1f1f1;
  border-bottom: 1px solid #9a9a9a;
}
.cartcontainer .emptycart {
  margin-top: 30px;
  margin-left: 40%;
}
.cartcontainer .emptycart h2 {
  font-size: 32px;
  color: #010f1c;
  text-transform: uppercase;
  font-weight: 300;
  margin-bottom: 30px;
}
.cartcontainer .emptycart .emptycartbtn {
  margin-left: 70px;
  text-decoration: none;
  color: #fff;
  padding: 10px 20px;
  background: #010f1c;
}
.cartcontainer .contant {
  max-width: 100%;
  padding: 20px 30px;
}
.cartcontainer .contant .cart_item {
  padding: 20px 30px;
  display: flex;
  background: #fff;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.cartcontainer .contant .cart_item .img_box {
  padding: 20px 20px;
  background: #f1f1f1;
}
.cartcontainer .contant .cart_item .img_box img {
  width: 250px;
  height: 250px;
}
.cartcontainer .contant .cart_item .detail {
  margin-left: 60px;
  display: flex;
  margin-top: 30px;
  padding: 20px 30px;
}
.cartcontainer .contant .cart_item .detail .info h4 {
  text-transform: uppercase;
  font-size: 16px;
  color: #9a9a9a;
  font-weight: 100;
  letter-spacing: 1.5px;
}
.cartcontainer .contant .cart_item .detail .info h3 {
  margin-top: 20px;
  color: #010f1c;
  font-size: 22px;
  font-weight: 200;
  letter-spacing: 1px;
}
.cartcontainer .contant .cart_item .detail .info p {
  margin-top: 10px;
  color: #010f1c;
  letter-spacing: 1px;
}
.cartcontainer .contant .cart_item .detail .info .qty {
  margin-top: 10px;
  display: flex;
}
.cartcontainer .contant .cart_item .detail .info .qty .incqty {
  padding: 10px 10px;
  background: none;
  outline: none;
  border: none;
  color: #0989ff;
  font-size: 32px;
  cursor: pointer;
}
.cartcontainer .contant .cart_item .detail .info .qty .decqty {
  padding: 10px 10px;
  background: none;
  outline: none;
  border: none;
  color: #0989ff;
  font-size: 32px;
  cursor: pointer;
}
.cartcontainer .contant .cart_item .detail .info .qty input {
  border: none;
  outline: none;
  background: none;
  font-size: 22px;
  color: #010f1c;
  width: 50px;
  margin-left: 20px;
}
.cartcontainer .contant .cart_item .detail .info .subtotal {
  color: #010f1c;
  font-size: 22px;
  margin-top: 10px;
}
.cartcontainer .contant .cart_item .detail .close {
  margin-top: 30px;
  margin-left: 50px;
}
.cartcontainer .contant .cart_item .detail .close button {
  border: none;
  outline: none;
  font-size: 22px;
  color: #db0045;
  background: none;
  cursor: pointer;
}
.cartcontainer .totalprice {
  margin-top: 30px;
  margin-left: 50%;
  font-size: 22px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.cartcontainer .checkout {
  margin-top: 40px;
  margin-left: 53%;
  padding: 10px 20px;
  color: #fff;
  border: none;
  outline: none;
  background: #0989ff;
  cursor: pointer;
}
